

.SingleTweetText {
    font-size: 12px;
    -webkit-transition: border-bottom 1.83s;
    -o-transition: border-bottom 1.83s;
    transition: border-bottom 1.83s;
}


.ProfileImg {
    width: 48px;
    height: 48px;
    border-radius: 50%;

}


.SingleTweetText :hover {
    cursor: pointer;
}


.Backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
}


