.SearchBar {
    width: 100%;
    height: 8%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row;
        flex-flow: row;
    -ms-flex-align: center;
        align-items: center;
    background-color: #000000;
}

.form-control-lg {
    padding: .1rem 0.8rem;
    font-size: 2.5rem;
    line-height: 1.5;
    border-radius: 0;
    border: 0px;
    width: 100%;
    height: 100%;
    background-color:#00aced;
    color: #ffffff;
}



.form-control-lg::-webkit-input-placeholder {
    color: #c0deed;
  }



.form-control-lg:-ms-input-placeholder {
    color: #c0deed;
  }



.form-control-lg::-ms-input-placeholder {
    color: #c0deed;
  }



.form-control-lg::placeholder {
    color: #c0deed;
  }


  .form-control-lg:focus {
    color: #0084b4;
  }


